import { defineComponent } from "vue";
import BlockProperties from "@/components/BlockProperties/index.vue";

export default defineComponent({
  name: "EditorBlockComponent",
  props: {
    id: Number,
    nodeId: Number,
    name: String,
    description: String,
    logo_class: String,
  },
  components: {
    BlockProperties,
  },
  data() {
    return {
      showProperties: false,
      type: "",
    };
  },
  methods: {
    toggleSettings(st: boolean) {
      if (st) {
        this.showProperties = st;
      } else {
        this.showProperties = !this.showProperties;
      }
    },
  },
  mounted() {
    let type;
    switch (this.id) {
      case 7:
        type = "contract";
        break;
      case 6:
        type = "message";
        break;
      default:
        type = "unknown";
        break;
    }
    this.type = type;
  },
});
